import React, { useRef } from "react";
import { useGLTF } from "@react-three/drei";

export function Hierarchy(props) {
  const { nodes, materials } = useGLTF("/glossarySelected/hierarchy.glb");
  return (
    <group {...props} dispose={null}>
      <group //position={[3.08, -1.03, -2.21]}
      >
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere163.geometry}
          material={materials["glass.004"]}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere163_1.geometry}
          material={materials["glass.001"]}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere163_2.geometry}
          material={materials.outline}
        />
      </group>
    </group>
  );
}
export default Hierarchy;
useGLTF.preload("/glossarySelected/hierarchy.glb");
